import Post from './Post';
import './Forum.scss';

function Forum(props) {
    return <div>
        <div className="ForumHeader">
        {props.header}
        </div>
    <div class="ForumSubContainer">
    {Object.values(props.posts).map( (p, idx) => {
        return <Post 
        key={idx} 
        header={p.header}
        years={p.years} 
        lines={p.lines}
        ></Post>})}
    </div></div>
}

export default Forum;